@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

.shop_watch {
}

.shop_watch .logo {
  position: absolute;
  left: 10%;
  margin-top: 25px;
  z-index: 2;
}

.shop_watch .logo h1 {
  color: white;
  font-family: "Lobster", cursive;
  font-size: 50px;
}

.shop_watch .navigation {
  position: absolute;
  margin-top: 25px;
  margin-left: 30%;
  width: 900px;
  color: black;
  z-index: 2;
}

.shop_watch .tab {
  text-decoration: none;
  cursor: pointer;
  font-size: 15px;
}

.shop_watch .navigation-right {
  position: absolute;
  z-index: 99;
  margin-top: 30px;
  right: 10vw;
}

.shop_watch .navigation-right-icon {
  font-size: 35px;
  padding-left: 10px;
}
.shop_watch .top_index {
  z-index: 2;
}

.shop_watch .banner {
}

.shop_watch .banner img {
  width: 100vw;
  height: 70vh;
  min-height: 650px;
  object-fit: cover;
}

.shop_watch .custom-caption {
}

.shop_watch .custom-caption h1 {
  color: #a06d0f;
}

.shop_watch .custom-caption p {
  color: white;
}

.shop_watch .item-container-large {
  z-index: 99;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 5vh;
}
.shop_watch .item-container-large h1 {
  color: #626261;
}

.shop_watch .row {
  margin-bottom: 50px;
}
.shop_watch .card-box {
  display: inline-block;
  width: 100%;
  margin-right: auto;
}
.shop_watch .card-box img {
  width: 80%;
  height: 80%;
  z-index: 1;
}
.shop_watch .card-box .sale {
  background-color: lightcoral;
  border-radius: 50%;
  margin-top: 4%;
  width: 50px;
  height: 50px;
  line-height: 50px;
  z-index: 99;
  position: absolute;
  text-align: center;
}
.shop_watch .card-box .sale span {
  font-size: 20px;
  margin-top: 15px;
}
.shop_watch .card-box .out-of-stock {
  background-color: white;
  opacity: 0.4;
  margin-top: 30%;
  width: 60%;
  margin-left: 15%;
  height: 50px;
  line-height: 50px;
  z-index: 99;
  position: absolute;
  text-align: center;
}

.shop_watch .card-box .out-of-stock span {
  font-size: 20px;
  margin-top: 15px;
  color: black;
  opacity: 1;
}

.shop_watch .content-box {
}

.shop_watch .content-box .category {
  font-size: 12px;
  color: #686868;
  top: 0;
  bottom: 0;
}
.shop_watch .content-box .model {
  color: black;
}
.shop_watch .content-box .color {
  display: block;
}
.shop_watch .content-box .color-text {
  color: black;
  font-weight: bold;
  font-size: 15px;
  display: inline-block;
}
.shop_watch .content-box .color-cirle {
  border-radius: 50%;
  content: "";
  height: 20px;
  width: 20px;
  display: inline-block;
  margin-left: 15px;
}
.shop_watch .content-box .original-price {
  display: inline-block;
  color: #686868;
  text-decoration: line-through;
  top: 0;
  bottom: 0;
}
.shop_watch .content-box .sale-price {
  display: inline-block;
  font-weight: bold;
  margin-left: 5%;
  top: 0;
  bottom: 0;
  color: black;
}

.shop_watch .section-bg {
  width: 100%;
}
.shop_watch .section-bg .section-box-left,
.shop_watch .section-bg .section-box-right {
  position: absolute;
  margin-top: 6vw;
  margin-bottom: auto;
  z-index: 3;
  width: 20%;
}
.shop_watch .section-box-left .section-icon,
.shop_watch .section-box-right .section-icon {
  font-size: 3vw;
}
.shop_watch .section-bg .section-box-left {
  left: 15%;
}
.shop_watch .section-bg .section-box-right {
  right: 15%;
}

.shop_watch .section-bg h3 {
  color: white;
  font-size: 2vw;
}
.shop_watch .section-bg p {
  color: white;
  font-size: 1vw;
}

.shop_watch .section-bg img {
  width: 100%;
  max-height: 25vw;
  min-height: 200px;
  object-fit: cover;
  overflow: hidden;
}

.shop_watch .section-bg2 {
  background-color: lightcoral;
  width: 100%;
}

.shop_watch .section-bg2-container {
  display: inline-block;
  position: relative;
  width: 50%;
  height: 300px;
}

.shop_watch .section-bg2-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.shop_watch .section-bg2-container .content-box {
  position: absolute;
  margin-left: 20%;
  margin-top: 10%;
  width: 60%;
}
.shop_watch .section-bg2 h3,
.shop_watch .section-bg2 a {
  color: white;
}
.shop_watch .slider_custom {
  padding-top: 50px;
}
.shop_watch .slider_custom h1 {
  color: #626261;
  padding-bottom: 50px;
  font-weight: bold;
}
.shop_watch .slider_custom .carousel-control-next-icon {
  background-image: url("website_templates/assets/icons/next-black.png");
}
.shop_watch .slider_custom .carousel-control-prev-icon {
  background-image: url("website_templates/assets/icons/previous-black.png");
}

.shop_watch .footer {
  background-color: black;
  padding-top: 30px;
  padding-bottom: 5px;
  width: 100%;
}

.shop_watch .footer h3,
.shop_watch .footer p {
  color: white;
}
.shop_watch .footer-container {
  width: 80%;
  margin-left: 10%;
}
.shop_watch .footer-col {
  margin-left: 2.5%;
  text-align: left;
}
