@import url("https://fonts.googleapis.com/css2?family=Cinzel");
.finimal {
  cursor: url("website_templates/assets/photo_video_finimal/mouse_default.png"),
    auto;

  height: 100%;
}

.finimal h1,
.finimal h2,
.finimal h3,
.finimal h4,
.finimal h5,
.finimal p,
.finimal strong,
.finimal a,
.finimal label {
  text-transform: uppercase;
  font-family: "Cinzel", sans-serif;
  font-weight: 600;
}
.finimal .video_container {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.finimal .navigation {
  position: fixed;
  z-index: 2;
  width: 80vw;
  justify-content: space-between;
  margin-top: 40px;
  margin-left: 10%;
  margin-right: 10%;
  color: black;
}

.finimal .tab {
  font-size: 50px;
  text-decoration: none;
  color: black;
  cursor: pointer;
  font-size: 20px;
}

.finimal .panel {
  min-height: 100%;
}

.finimal .title {
  display: block;
  position: absolute;
  z-index: 2;
  width: 70vw;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.finimal .title h1 {
  color: white;
  font-size: 7vw;
}
.finimal .projects {
  padding-bottom: 200px;
}

.finimal .projects a:hover {
  cursor: url("website_templates/assets/photo_video_finimal/mouse.png"), auto;
  text-decoration: none;
  color: rgb(92, 92, 92);
}

.finimal .projectBox {
  width: 80vw;
  padding-top: 60px;
  padding-bottom: 60px;
  margin-left: 10vw;
  text-align: left;
  border-bottom: 1px solid rgb(124, 124, 124);
}
.finimal .projectBox h1 {
  color: black;
  margin-top: 30px;
}

.finimal .footer {
  display: block;

  width: 100%;
  position: fixed;
  background-color: rgb(124, 124, 124);
  padding-top: 20px;

  bottom: 0px;
}
.finimal .footer p,
strong {
  font-family: "Times New Roman", Times, serif;
  text-transform: capitalize;
  color: black;
  font-size: 20px;
}
.finimal .footer p {
  font-weight: 400;
}
.finimal .footer strong {
  font-weight: 600;
}
.finimal .footer a:link {
  cursor: pointer;
}

.finimal .contact {
  width: 60%;
  margin-left: 20%;
  padding-top: 200px;
  padding-bottom: 100px;
}
.finimal .contact h1 {
  color: black;
  font-size: 7vw;
}

.finimal .contact label {
  color: black;
  float: left;
  margin-left: 2%;
  padding-top: 30px;
}
.finimal input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  color: black;
}

.finimal input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.finimal #message {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  color: black;
  padding-top: 30px;
  padding-bottom: 100px;
}

.finimal input[type="submit"] {
  width: 100%;
  background-color: black;
  color: white;
  padding: 14px 20px;
  margin: 40px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.finimal input[type="submit"]:hover {
  background-color: rgb(124, 124, 124);
}

.finimal .about {
  width: 60%;
  margin-left: 20%;
  padding-top: 200px;
  padding-bottom: 100px;
  text-align: left;
}
.finimal .about p,
h5,
li {
  font-family: "Times New Roman", Times, serif;
  text-transform: none;
  font-weight: normal;
  color: black;
}

.finimal .about ul {
  margin-left: 5%;
}
