@import url("https://fonts.googleapis.com/css2?family=Poppins");

.summer {
  display: flex;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  background: radial-gradient(#9cdcff, #1cafff);
  width: 100%;
  height: 50%;
  padding-top: 50vh;
  padding-bottom: 100vh;
}

.summer h2,
.summer span {
}

.summer .banner {
  position: relative;
  width: 100%;
  height: 100%;
  /*overflow: hidden;*/
}
.summer .container {
  position: absolute;
  width: 100%;
  height: 100%;
}
.summer .container #summerscene {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.summer .container #summerscene #summertext {
  position: absolute;
  display: flex !important;
  position: relative;
  color: #ffff;
  font-weight: bold;
  font-size: 14vw;
  z-index: 100;
}
.summer .container #summerscene #summertext span {
  position: relative !important;
  display: inline-block;
  text-shadow: 1px 1px 0 #e1e1e1, 2px 2px 0 #e1e1e1, 3px 3px 0 #e1e1e1,
    4px 4px 0 #e1e1e1, 5px 5px 0 #e1e1e1, 6px 6px 0 #e1e1e1, 7px 7px 0 #e1e1e1,
    8px 8px 0 #e1e1e1, 25px 35px 0 rgba(0, 0, 0, 0.1);
}
.summer .layer {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.summer .layer img {
  width: 80px;
}

.summer .zIndexAbove {
  z-index: 100;
}
.summer .img01 {
  padding-right: 52vw;
}
.summer .img02 {
  padding-right: 34vw;
  padding-top: 3vh;
}
.summer .img03 {
  padding-right: 16vw;
}
.summer .img04 {
  padding-left: 3vw;
  padding-top: 3vh;
}
.summer .img05 {
  padding-left: 25vw;
}
.summer .img06 {
  padding-left: 25vw;
  padding-top: 10vh;
}
.summer .img07 {
  padding-left: 52vw;
  padding-top: 3vh;
}
