  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;

  text-decoration: none;
  -webkit-animation-name: animationActive;
          animation-name: animationActive;
  -webkit-animation-duration: 4s;
